:root {
  --primary: rgb(71, 17, 141);
  --secondary: rgb(17, 2, 44);
  --dark: rgb(11, 9, 22);
  --light: white;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(209, 209, 209);
  height: 100vh;
}

.App {
  display: flex;
  min-height: 100vh;
  padding: 16px;
}

.calculator {
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  background-color: whitesmoke;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.2);
}

.display {
  padding: 16px;
  text-align: right;
  background-color: var(--secondary);
  color: var(--light);
  font-size: 24px;
  font-weight: 300;
}

.display span {
  font-size: 14px;
  color: #5c5959;
}

button {
  appearance: none;
  border: none;
  outline: none;
  color: white;
  font-size: 20px;
  padding: 16px;
  cursor: pointer;
  transition: 0.4s;
}

button:hover {
  opacity: 0.8;
}

.operators {
  display: flex;
}

.operators button {
  flex: 1 1 0%;
  background-color: var(--primary);
  font-weight: 700;
}

.digits {
  display: flex;
  flex-wrap: wrap;
}

.digits button {
  flex: 1 1 33.333%;
  max-width: 33.333%;
  background-color: var(--dark);
}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  background-color: var(--primary);
}

.footer a {
  margin: auto;
}

.footer a:hover{
  opacity: 0.5;
}

#portfolio {
  /* background-image: url('./portfolio.png'); */
  width: 30px;
  height: 25px;
  font-weight: bold;
  color: white;
  text-decoration: none;
}

